import React from 'react'
import Project from '../domain/Project'
import {SimpleGrid, Text, VStack} from '@chakra-ui/react'
import TagsGrid from './TagsGrid'
import ProjectBadge from './ProjectBadge'
import {formatDate} from '../util/date'
import Card from '../ui/Card'
import Badge from '../ui/Badge'

export interface ProjectCardProps {
  project: Project
}

const ProjectCard: React.FC<ProjectCardProps> = ({project}) => {
  const dateString = formatDate(project.hasStarted() ? project.dateEnd : project.dateStart)

  return (
      <Card>
        <SimpleGrid columns={3} spacing={10} templateColumns="1fr auto auto" alignItems="center">
          <Text fontWeight="bold">
            {project.name}
            {project.clientName && <Badge ml={2} colorScheme="purple" size="xs">{project.clientName}</Badge>}
          </Text>
          <TagsGrid tags={[
            {
              name: 'Team of 3',
            },
            {
              name: 'Flex'
            }
          ]}/>
          <VStack>
            <ProjectBadge project={project}/>
            {dateString && <Text fontSize="xs">{dateString}</Text>}
          </VStack>
        </SimpleGrid>
      </Card>
  )
}

export default ProjectCard