import React from 'react'
import Form from '../components/Form'
import useStore from '../hooks/useStore'
import {useHistory} from 'react-router-dom'
import {Input, VStack} from '@chakra-ui/react'
import FormField from '../components/FormField'
import Card from '../ui/Card'
import FormSubmitButton from '../components/FormSubmitButton'
import {parseISO} from 'date-fns'
import Project from '../domain/Project'

const CreateProject: React.FC = () => {
  const {projectStore, organizationStore} = useStore()
  const history = useHistory()

  const onSubmit = async ({ name, clientName, dateStart, dateEnd}: any) => {
    const project = await projectStore.create((new Project()).fromObject({
      organizationId: organizationStore.currentOrganizationId,
      name,
      clientName,
      dateStart: dateStart ? parseISO(dateStart) : null,
      dateEnd: dateEnd ? parseISO(dateEnd) : null
    }))

    if (project.isOngoing()) {
      history.push(`/my-projects/active-projects`)
    } else if (!project.hasStarted()) {
      history.push(`/my-projects/upcoming-projects`)
    } else {
      history.push('/my-projects/ended-projects')
    }
  }

  return (
      <Card>
        <Form onSubmit={onSubmit}>
          <VStack spacing={4} alignItems="start">
            <FormField
                name="name"
                required
                label="Project name"
            >
              <Input
                  size="lg"
                  autoFocus
              />
            </FormField>

            <FormField
                name="clientName"
                label="Client"
            >
              <Input />
            </FormField>

            <FormField
                name="dateStart"
                label="Starts at"
            >
              <Input type="date" />
            </FormField>

            <FormField
                name="dateEnd"
                label="Ends at"
            >
              <Input type="date" />
            </FormField>

            <FormSubmitButton>
              Create
            </FormSubmitButton>
          </VStack>
        </Form>
      </Card>
  )
}

export default CreateProject